//--------- useEffect -------


import { useEffect, useState } from "react";
// 1. useEffect(callback) // t dng
// - Gi callback mi khi component re-render
// - Gi callback sau khi element thm vo DOM
// 2. useEffect(callback, [])
// - Gi callback 1 ln khi component mounted
// - Gi API
// 3. useEffect(callback, [deps])
// - Callback s c gi li mi khi deps (bin) thay i

//---------------
// 1. Callback lun c gi sau khi component mounted
// 2. Cleanup function lun c gi trc khi component unmounted
// 3. Cleanup function lun c gi trc khi callback() c gi (tr ln mounted)


const tabs = ['posts', 'todos', 'users']
function Ex2() {
    // const [title, setTitle] = useState();
    const [todos, setTodos] = useState([])
    const [type, setType] = useState('posts')
    //useEffect s run sau return
    useEffect(() => {
        // document.title = title;
        fetch(`https://jsonplaceholder.typicode.com/${type}`)
            .then(res => res.json())
            .then(todosApi => {
                setTodos(todosApi)
            })

        //Cleanup func trnh r r b nh
        return () => {
            // fuction thc thin khi out useEffect()
        }
    }, [type])
    return (
        <div className="">
            <h1> useEffect </h1>
            <ChatFake/>
            <div>
                {tabs.map(tab => (
                    <button
                        key={tab}
                        style={type === tab ? { opacity: "0.8" } : {}}
                        onClick={() => setType(tab)}
                    >{tab}</button>
                ))}
            </div>
            <div className="ContentAPI">
                <ul>
                    {todos.map(todo => (
                        <li key={todo.id}>{todo.title || todo.name}</li>
                    ))}
                </ul>
            </div>
        </div>
    )
}

const lessions = [
    { id: 1, name: "What's ReactJS?" },
    { id: 2, name: "What's SPA/SPM?" },
    { id: 3, name: "useState" },
    { id: 4, name: "useEffect" }]
function ChatFake() {
    const [lessionId, setLessionId] = useState(1)

    return (
        <div>
            <ul>
                {lessions.map(lession => (
                    <li
                        key={lession.id}
                        style={{color:lessionId ===lession.id ? 'red' : '#333'}}
                        onClick={()=>setLessionId(lession.id)}
                    >
                        {lession.name}
                    </li>
                ))}
            </ul>
        </div>

    )
}



export default Ex2