//------useCallback--------
// 1.  memo() -> Higher Oder Component (HOC)
// - dng ton t ===  so snh props (tham chiu), nu k c g thay i n s k run
// - syntax: export default memo(Ex4)
// - use khi state k thay i
// 2. useCallback( ()=>{} , [] )
// - Reference types
// - memo() so snh tham chiu ===
// - nu tham chiu k thay i, useCallback k to li function

import { useState, useCallback } from "react";
import Ex4Child from "./Ex4-child";
function Ex4() {
    const [count4, setCount4] = useState(0)
    const handleCount4 = useCallback(() => {
        setCount4(prevCount4 => prevCount4 + 1)
    },[])

    return (
        <div>
            <Ex4Child onCount4={handleCount4} />
            <h2>{count4}</h2>
        </div>
    )
}

export default Ex4