
// Context: Comp A => Comp C truyn xuyn khng
// Comp A => Comp B => Comp C
// 1. a = createContext()
// 2. Provider // c th chia ra thnh 1 comp
// 3. Consumer // useContext(a) dng  comp C

//----To Theme: Dark / Light
import { Button } from "@mui/material";
import { createContext, useState } from "react";
import Ex4 from "./Ex4";

const ThemeContext = createContext();
function Ex7() {
    const [theme, setTheme] = useState('light')

    const toggleTheme = () => {
        setTheme(theme === 'dark' ? 'light' : 'dark')
    }

    return (
        <ThemeContext.Provider value={theme}>
             <Button variant="contained"
                onClick={toggleTheme}
            >Dark / Light</Button>
            <Ex4></Ex4>
        </ThemeContext.Provider>
    )

}
export {ThemeContext}// c th dng cch ny export nhiu thnh phn
export default Ex7 // export default bt buc